.hyperbola {
    width: 200px;
    height: 200px;
    background-color: #0074d9;
    position: relative;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  }
  .hyperbola::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0074d9;
    border-radius: 50%;
    transform: scaleX(2);
  }