@import '../../../theme/theme.css';



.signup-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: '#E7EDEE';
  
}
@keyframes floatUpDown {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px); /* Adjust the distance the div floats up */
    }
  }
  



.signup-title{
    margin-block: 20px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    animation: floatUpDown 2s ease-in-out infinite;
    color: gray; font-family: 'Open Sans', sans-serif; font-size: 30px; font-weight: 600; line-height: 32px; margin: 0 0 72px; text-align: center;
}
.signup-btn{
    width: 15%;
    height: 40px;
    border-radius: 20px;
    margin-top: 3%;
    animation: popUp 0.5s ease-in-out forwards;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    color: #1e1e1e;
    justify-content: center;
    align-items: center;
    display: flex;
    border-top: 2px solid #ffffff; /* 2px width and color #333 (adjust as needed) */
}
.signup-btn:hover{
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
}
.roundedDiv {
    width: 20%;
    height: 80px;
    
    border-radius: 20px;
    margin: 10px;
    animation: popUp 0.5s ease-in-out forwards;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
    color: #202020;
    justify-content: center;
    align-items: center;
    display: flex;
    box-shadow: 1px 1px 2px gray;
  }
  .select-MuiInputBase-input-MuiOutlinedInput-input{
    color: white !important;
  }

  .roundedDiv-sel {
    width: 20%;
    height: 80px;
    
    border-radius: 20px;
    margin: 10px;
    text-shadow: 1px 1px 1px 4px rgba(0, 0, 0, 0.5);
    color: #262626;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 1px 2px rgb(79, 144, 29);
    display: flex;
    cursor: pointer;
    border-top: 10px solid #616161; /* 2px width and color #333 (adjust as needed) */
  }

  @keyframes popUp {
    0%{
        transform:scale(0);
        opacity: 0;
    }
    100%{
        transform: scale(1);
        opacity: 1
    }
  }
 
  
  .roundedDiv:hover{
    border-top: 10px solid #ffffff89;
    cursor: pointer; /* Add cursor back here if needed */
  }
  
  /* Remove animation on hover */
  .roundedDiv:hover::after {
    content: ''; /* Add an empty pseudo-element */
    animation: none; /* Disable animation on hover */
  }
.sidebar{
    display: flex;
    flex-direction: column;
    
}

.carousel-root{
    display: flex;
    width:100% !important;
    height: 100vh !important;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.carousel-slider{
    display: flex;
    width: 90% !important;
    height: 80vh !important;
    margin: auto;
    justify-content: center;

}
.slider-wrapper{
    display: flex;
    height: 100% !important;
}